import htmlParser from '@/common/html-parser'
import uniGoodsNav from '@/components/uni-goods-nav/uni-goods-nav.vue'


const FAIL_CONTENT = '<p>获取信息失败1</p>';

function parseImgs(nodes) {
	nodes.forEach(node => {
		if (
			node.name === 'img' &&
			node.attrs &&
			node.attrs['data-img-size-val']
		) {
			const sizes = node.attrs['data-img-size-val'].split(',')
			const width = uni.upx2px(720 * 0.9)
			const height = parseInt(width * (sizes[1] / sizes[0]))
			node.attrs.style = `width:${width};height:${height};`
		}
		if (Array.isArray(node.children)) {
			parseImgs(node.children)
		}
	})
	return nodes 
}

export default {
	components: {uniGoodsNav},
	data() {
		return {
			banner: {},
			content: [],
			  options: [{
			          icon: '&#xe63a;',
			          text: '评论',
					  url: "../comment/comment"
			        }, {
			          icon: '&#xe60c;',
			          text: '点赞'
			        }, {
			          icon: '&#xe73f;',
			          text: '不喜欢',
			         
			        }, {
			          icon: '&#xe612;',
			          text: '收藏',
			         
			        }],
			        buttonGroup: [{
			          text: '',
			          backgroundColor: '#ff0000',
			          color: '#fff'
			        }
			        ]
		}
	},
	onShareAppMessage() {
		return {
			title: this.banner.title,
			path: '/pages/detail/detail?query=' + JSON.stringify(this.banner)
		}
	},
	onLoad(event) {
		// 目前在某些平台参数会被主动 decode，暂时这样处理。
		try {
			this.banner = JSON.parse(decodeURIComponent(event.query));
		} catch (error) {
			this.banner = JSON.parse(event.query);
		}

		uni.setNavigationBarTitle({
			title: this.banner.title
		});

		this.getDetail();
	},
	methods: {
		getDetail() {
			uni.request({
				url: 'https://unidemo.dcloud.net.cn/api/news/36kr/' + this.banner.post_id,
				success: (result) => {
					let content = FAIL_CONTENT
					if (result.statusCode == 200) {
						content = result.data.content
					}
					const nodes = htmlParser(content);



					this.content = nodes
				}
			});
		},
		
		     onClick (e,item) {
				 console.log("??")
				 if(e.content.url){
					 uni.navigateTo({
					 		url: e.content.url
					 });
				 }else {
					 uni.showToast({
					   title: `点击${e.content.text}`,
					   icon: 'none'
					 })
				 }
		       
		      },
		      buttonClick (e) {
		        console.log(e)
		        this.options[2].info++
		      }
	}
}
